//  : 
import Navigation from "../components/Navigation";
// data
import data from "../assets/DB/aboutData";
//
import "./about.scss";
//
import img from "../assets/images/gallery2.jpg";
import pinkSparkling from "../assets/images/pink-sparkling.png";
import GoUp from "../components/GoUp";

function About() {
  return (
    <div className="tabContainer">
      <Navigation />
      <div className="about">
        <div className="wrapper">
          <div className="intoduce">
            <img src={img} alt="pic" />
            <div className="txt">
              <p>
                      .
                        
                .
              </p>
              <p>
                       
                .
              </p>

              <div className="tag">
                <span>#</span>
                <span>#</span>
                <span>#</span>
                <span>#_</span>
              </div>

              <button className="connect">let's connect</button>
            </div>
          </div>

          <div className="skill">
            <h2 className="headline">
              skill
              <img
                src={pinkSparkling}
                alt=" "
                className="pinkSparkling"
              />
            </h2>
            {data.map((item) => (
              <section key={item.id}>
                <h4>{item.name}</h4>
                <ul>
                  <li>{item.des1}</li>
                  <li>{item.des2}</li>
                  <li>{item.des3}</li>
                  <li>{item.des4}</li>
                  <li>{item.des5}</li>
                  <li>{item.des6}</li>
                  <li>{item.des7}</li>
                  <li>{item.des8}</li>
                  <li>{item.des9}</li>
                  <li>{item.des10}</li>
                </ul>
              </section>
            ))}
          </div>

          <div className="contact">
            <h4>kim soohyun</h4>
            <h5>010-6481-0829</h5>
            <h5>skim0829@outlook.com</h5>
          </div>
        </div>
      </div>

      <GoUp />
    </div>
  );
}

export default About;
